import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'angular-weblineindia-email',
  templateUrl: './angular-weblineindia-email-input.component.html',
  styleUrls: ['./angular-weblineindia-email-input.component.css'],
})
export class AngularWeblineindiaEmailInputComponent {
  @Input() placeholder!: string;
  @Input() disabled: boolean = false;
  @Input() value!: string;
  @Input() required: boolean = false;
  @Input() isInvalidEmail: boolean = false;
  @Input() invalidMessage!: string;
  @Input() class!: string;
  @Input() errorClass!: string;

  @Output() focus: EventEmitter<void> = new EventEmitter<void>();
  @Output() blur: EventEmitter<void> = new EventEmitter<void>();
  @Output() KeyPress: EventEmitter<KeyboardEvent> =
    new EventEmitter<KeyboardEvent>();
  @Output() KeyDown: EventEmitter<KeyboardEvent> =
    new EventEmitter<KeyboardEvent>();
  @Output() KeyUp: EventEmitter<KeyboardEvent> =
    new EventEmitter<KeyboardEvent>();

  constructor() {}

  onFocus(event: any) {
    this.focus.emit(event);
  }

  onBlur(event: any) {
    this.blur.emit(event);
  }

  onKeyPress(event: KeyboardEvent): void {
    this.KeyPress.emit(event);
  }

  onKeyDown(event: KeyboardEvent): void {
    this.KeyDown.emit(event);
  }

  onKeyUp(event: KeyboardEvent): void {
    this.KeyUp.emit(event);
  }
}
